<script>
import { getLodop } from "common/utils/LodopFuncs";

let LODOP = getLodop;
export default {
  data() {
    return {
      P_ID: null,
      params: null,
      id: null,
      tableData: null
    };
  },
  methods: {
    list(val) {
      let vm = this;
      this.$axios({
        method: "get",
        url: this.$api.printClaim,
        params: {
          company_claim_goods_id: val,
          repeat: true
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.params = res.data.data.info;
          this.tableData = res.data.data.goods_list;
          setTimeout(function() {
            vm.CreatePrintPage();
          }, 0);
        }
      });
    },
    printPdf(val) {
      this.id = val.join();
      this.list(val.join());
    },
    CreatePrintPage() {
      let This = this;
      var LODOP = getLodop();
      if (!LODOP) {
        return false;
      }
      var strStyle = LODOP.strStyle; //临时处理尺寸
      LODOP.SET_PRINT_MODE("AUTO_CLOSE_PREWINDOW", 1); // 打印后自动关闭预览
      LODOP.SET_PRINT_MODE("CUSTOM_TASK_NAME", "发货单"); // 打印队列中的文档名
      LODOP.ADD_PRINT_TABLE(
        92,
        "2%",
        "95%",
        415,
        document.getElementById("print-body").innerHTML
      );
      LODOP.SET_PRINT_STYLEA(0, "Vorient", 3);
      LODOP.ADD_PRINT_HTM(
        0,
        "2%",
        "95%",
        83,
        document.getElementById("print-header").innerHTML
      );
      LODOP.SET_PRINT_STYLEA(0, "ItemType", 1);
      LODOP.SET_PRINT_STYLEA(0, "LinkedItem", 1);
      LODOP.ADD_PRINT_HTM(
        526,
        "2%",
        "95%",
        58,
        document.getElementById("print-footer").innerHTML
      );
      LODOP.SET_PRINT_STYLEA(0, "ItemType", 1);
      LODOP.SET_PRINT_STYLEA(0, "LinkedItem", 1);
      LODOP.NewPageA();
      // LODOP.ADD_PRINT_HTM(1, 700, 300, 100,
      //  `<span style="font-size:15px">第${this.params.print_times}次打印</span>`
      // );
      LODOP.SET_PRINT_STYLEA(0, "ItemType", 1);
      LODOP.SET_PRINT_STYLEA(0, "Horient", 1);
      LODOP.ADD_PRINT_HTM(
        3,
        34,
        196,
        20,
        "<span tdata='pageNO'  style='font-size:15px'>第##页</span>/<span tdata='pageCount' style='font-size:15px'>共##页</span>"
      );
      LODOP.SET_PRINT_STYLEA(0, "ItemType", 1);
      LODOP.PREVIEW();
      LODOP.On_Return_Remain = true;
      LODOP.On_Return = function(TaskID, Value) {
        This.P_ID = Value;
        if (Value == 1) {
          This.$axMethod
            .printTimesAdd({
              bill_id: This.id,
              type: 12
            })
            .then(data => {
              This.$emit("Search", "");
              // console.log(data.data)
            });
          // alert("打印成功！");
        } else if (Value == 0) {
          // alert("打印任务被删除！");
        }
      };
    }
  }
};
</script>
