<template>
  <div>
    <div id="leixin">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="销售组织" size="mini">
            <el-select clearable v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" @change="companyChange" filterable>
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': ''">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="客户名称" size="mini">
            <el-select v-model="params.customer_id" filterable placeholder="请输入" clearable @focus="clienteleListInvok" @change="customerListChange" :loading="loading" remote :remote-method="clienteleListInvok">
              <ul class="customer customer-ul4 customer-ul2">
                <li>客户编号</li>
                <li>客户名称</li>
                <li>联系人</li>
                <li>联系电话</li>
                <li>区域</li>
              </ul>
              <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
                <ul class="customer customer-ul4">
                  <li :title="item.customer_id">{{ item.customer_id }}</li>
                  <li :title="item.customer_name">{{ item.customer_name }}</li>
                  <li :title="item.contact_name">{{item.contact_name}}</li>
                  <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                  <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
                </ul>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="部门" size="mini">
            <el-select clearable v-model="params.department_id" @focus="DepartmentListInvok" @change="departmentChange" placeholder="请选择" filterable>
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="商品名称" size="mini">
            <el-input v-model="params.goods_name" placeholder="请输入" clearable  @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>
          <el-form-item label="公司理赔单据号" size="mini">
            <el-input v-model="params.company_claim_sn" placeholder="请输入" clearable  @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>
          <el-form-item label="关联公司鉴定单号" size="mini">
            <el-input v-model="params.company_appraisal_sn" placeholder="请输入" clearable  @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="审核日期" size="mini">
            <el-date-picker v-model="timeValue2" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="审核状态" size="mini">
            <el-select v-model="params.auditor_status" clearable>
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未审核" :value="1">未审核</el-option>
              <el-option label="已审核" :value="2">已审核</el-option>
              <el-option label="作废" :value="3">作废</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="办理状态" size="mini">
            <el-select v-model="params.manage_status" placeholder="请选择">
              <el-option label="全部" :value="0"></el-option>
              <el-option label="未办理" :value="1"></el-option>
              <el-option label="三包折款" :value="2"></el-option>
              <el-option label="三包销售" :value="3"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="胎号" size="mini">
            <el-input v-model="params.tire_un" placeholder="请输入" clearable  @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>
          <el-form-item label="病象" size="mini">
            <el-input v-model="params.symptom_name_str" placeholder="请输入" clearable  @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>
          <el-form-item label="" size="mini">
            <el-checkbox style="margin-left:110px;" false-label="0" true-label="1" v-model="params.aftersales_status" name="type">照顾理赔</el-checkbox>
          </el-form-item>
          <el-form-item size="mini">
            <el-checkbox v-model="params.is_zero" false-label="0" true-label="1" name="type" style="margin-left:110px;">打印次数为0</el-checkbox>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="salesAddF" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
        <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del==1">删除</el-button>
        <el-button @click="audit_enable('shenhe')" size="mini" icon="iconfont icon-pass" type="primary" v-if="authority.b_pass==1">审核</el-button>
        <el-button @click="audit_enable('priceShenhe')" size="mini" icon="iconfont icon-pass" type="primary" v-if="authority.b_passprice==1">价格审核</el-button>
        <el-button plain @click="audit_enable('cancelShenhe')" icon="iconfont icon-fanshenhe" v-if="authority.b_nopass==1">弃审</el-button>
        <el-button plain icon="iconfont icon-zuofei" @click="audit_enable('zuofei')" size="mini" v-if="authority.b_invalid==1">作废</el-button>
        <el-button @click="downLoadFile" plain icon="el-icon-download" size="mini" v-if="authority.b_export_file==1">下载模板</el-button>
        <el-button plain icon="iconfont icon-daoru5" v-if="authority.b_import==1" @click="dialogEmployee" class="addBtn" size="mini">期初导入</el-button>
        <el-button plain size="mini" icon="el-icon-printer" @click="printF" v-if="authority.b_print==1">批量打印</el-button>
        <el-dropdown style="margin:0 10px;" v-if="authority.b_export==1">
          <span split-button style="cursor:pointer;color:#4e90e2">
            <el-button plain icon="iconfont icon-daochu10" size="mini">导出</el-button>
          </span>
          <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
            <el-dropdown-item @click.native="exportOrder(1)">导出Excel文件</el-dropdown-item>
            <el-dropdown-item @click.native="exportOrder(2)">导出CSV逗号分隔</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div class="table_container">
        <xTable :ajax='{ url: "getclaimList", params: params }' ref='xTable' :tableColumn='tableColumn' />
      </div>
      <upload :options="options" @receiveUploadMsg="onSearch(1)" ref="upload"></upload>
      <div>
        <printCompanyclaim ref="lodopDelivery" @Search="onSearch"></printCompanyclaim>
      </div>
    </div>
  </div>
</template>
<script>
import printCompanyclaim from "common/components/public/printCompanyclaim.vue"; //出库单打印组件
import { TableHead } from "./config/companyclaim_Index";
export default {
  name: "aftersaleCompanyclaimIndex",
  mixins: [TableHead],
  data() {
    return {
      options: {
        title: "公司理赔",
        url: "/v2/aftersale/CompanyClaim/importCompanyClaim"
      },
      timeValue2: [],
      params: {
        aftersales_status: 0,
        manage_status: 0
      },
      loading:false,
      dialogFormVisible: false
    };
  },
  components: {
    printCompanyclaim
  },
  methods: {
    customerListChange(val) {
      if (val) {
        this.params.customer_name = this.customerList.filter(
          obj => obj.customer_id === val
        )[0].customer_name;
      }
    },
    dialogEmployee() {
      this.$refs.upload.stateOpen();
    },
    departmentChange(val) {
      if (val) {
        this.params.department_name = this.departmentList.filter(
          obj => obj.department_id === val
        )[0].department_name;
      }
    },
    exportOrder(scheduleType) {
      // 导出库存收发明细表导出URL
      let s = Object.assign(this.params, {
        export_type: scheduleType
      });
      this.$axMethod.aftersaleExportCompanyClaimUrl(s).then(response => {
        if (response.data.code == 200) {
          window.location.href = response.data.data.url;
        }
      });
    },
    printF() {
      let companyFlag = false;
      const companyIds = this.multipleSelection;

      for (var i = 1; i < companyIds.length; i++) {
        if (companyIds[i - 1].company_id != companyIds[i].company_id) {
          companyFlag = true;
          break;
        }
      }
      if (!this.multipleSelection.length) {
        this.$message({
          message: "请选择单据进行打印",
          type: "warning"
        });
      } else if (companyFlag) {
        this.$message({
          message: "请选择相同公司单据进行打印",
          type: "warning"
        });
      } else {
        let flag = true,
          errorGoodsArr = [];
        this.multipleSelection.forEach(item => {
          if (item.after_auditor_print_status == 1) {
            //审核后才可打印
            if (item.auditor_status != 2) {
              errorGoodsArr.push(item.company_claim_sn);
              flag = false;
            }
          }
        });

        if (flag) {
          let arryId = this.multipleSelection.map(item => {
            return item.company_claim_goods_id;
          });
          this.$refs.lodopDelivery.printPdf(arryId);
        } else {
          let str = errorGoodsArr.map((item, i) => `${i + 1}.${item}`);
          this.$alert(
            `以下未审核的单据不可打印：<br/>
            <div style="color:#e6a23c">
              ${str.join("，")}
            <div>`,
            "提示",
            {
              dangerouslyUseHTMLString: true
            }
          );
        }
      }
    },
    downLoadFile() {
      // 下载模板
      this.$axMethod.afterdownloadFileUrl().then(response => {
        if (response.data.code == 200) {
          window.location.href = response.data.data.url;
        }
      });
    },
    salesModifyF(row) {
      this.$router.push({
        path: "/aftersale/companyclaim/companyclaim_modification",
        query: {
          id: row.company_claim_goods_id
        }
      });
    },
    salesAddF() {
      this.$router.push({
        path: "/aftersale/companyclaim/companyclaim_add"
      });
    },
    onReset() {
      this.$set(this, "params", {
        aftersales_status: 0,
        manage_status: 0
      });
      this.timeValue = [];
      this.timeValue2 = [];
      this.onSearch(1)
    },
    onSearch(number) {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.business_start_time = this.timeValue[0];
        this.params.business_end_time = this.timeValue[1];
      } else {
        this.params.business_start_time = this.params.business_end_time = "";
      }
      if (this.timeValue2 && this.timeValue2.length == 2) {
        this.params.auditor_start_time = this.timeValue2[0];
        this.params.auditor_end_time = this.timeValue2[1];
      } else {
        this.params.auditor_start_time = this.params.auditor_end_time = "";
      }
      this.$refs.xTable._getData(number);
    },
    deletes() {
      var deledata = {
        deleID: "company_claim_goods_id",
        deleSelect: this.multipleSelection,
        deleApi: "delClaim",
        list: {
          UrlApi: "getclaimList",
          data: this.TableData,
          esData: Object.assign({}, this.params)
        }
      };
      this.$common.deletes(deledata, "删除", this, this.onSearch);
    },
    auditF(warningMsg, API) {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: `请选择需要${warningMsg}的信息`,
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].company_claim_goods_id);
      }
      var rebateArr = addr.toString();
      this.$axMethod[API]({
        company_claim_goods_id: rebateArr
      }).then(response => {
        if (response.data.code == 200) {
          this.$message({
            message: response.data.msg ? response.data.msg : response.data.data,
            type: "success"
          });
          this.onSearch();
        }
      });
    },
    audit_enable(name) {
      let warningMsg, API;
      if (name == "shenhe") {
        warningMsg = "审核";
        API = "auditorPassCompanyClaim";
      } else if (name == "priceShenhe") {
        warningMsg = "价格审核";
        API = "auidtorPassPrice";
        this.$confirm(
          "价格审核将会更新【最新销售价】为现在的最新销售价，并且会按三包计算公式【理赔单价】=【最新销售价】-【磨损费】-【内垫费】重新计算【理赔单价】。",
          "是否继续审核？",
          {
            distinguishCancelAndClose: true,
            confirmButtonText: "确定",
            cancelButtonText: "取消"
          }
        )
          .then(() => {
            this.auditF(warningMsg, API);
          })
          .catch(action => {
            this.$message({
              type: "info",
              message: action === "cancel" ? "已取消价格审核操作" : "请重新操作"
            });
          });
      } else if (name == "cancelShenhe") {
        warningMsg = "弃审";
        API = "auidtorNoPass";
      } else if (name == "zuofei") {
        warningMsg = "作废";
        API = "auditorInvalidlp";
      }

      if (["shenhe", "cancelShenhe", "zuofei"].includes(name))
        this.auditF(warningMsg, API);
    },
    companyChange(val) {
      this.$set(this.params, "customer_id", null); // 初始花客户下拉选中值
      this.$set(this.params, "department_id", null); // 初始化部门下拉选中值
      this.$set(this.params, "customer_id", ""); // 初始化客户名称下拉选中值
      if (val) {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
    }
  },
  created() {
    this.hasAuditorStatusPrint = window.localStorage.hasAuditorStatusPrint;
  },
  activated() {
    this.onSearch(window.goPage);
  }
};
</script>
